.album-detail{
    position: relative;
    overflow: hidden;
    background: #fdfdfd;
    .mask{
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50%;
        filter: blur(10px);
        transform: scale(1.3);
    }
    .mask:after{
        content: " ";
        background-color: rgba(0,0,0,.25);
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
    }
    .listen {
        background-image: url(data:image/svg+xml;base64,);
        padding-left: 13px;
        background-repeat: no-repeat;
        background-position: 0 3px;
        background-size: 11px 10px;
        text-shadow: 1px 0 0 rgba(0, 0, 0, 0.15);
        font-size: 12px;
        color: #fff;
        position: absolute;
        left: 60px;
        top: 5px;
        z-index: 3;
    }
}
.ant-row{
    text-align:justify
